import React from 'react';
import '../css/App.css';
import Axios from 'axios';
import { withCookies } from 'react-cookie';
import GoogleLogin from 'react-google-login';

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      googleUrl: '',
      loggedIn: false
    }
  }

  responseGoogle(resp) {
    Axios.post(`/logincomplete?code=${resp.code}`)
    .then((response) => {
      this.props.cookies.set('access_token', response.data.access_token);
      this.props.cookies.set('id_token', response.data.id_token);
      this.setState({
        loggedIn: true
      })
    })
    .catch((error) => {
      alert(`Error: ${JSON.stringify(error)}`);
    })
  }

  addLaunch() {
    Axios.post(`/launch?token=${this.props.cookies.id_token}`)
  }

  render() {
    return (
      <div className="App">
        {!this.state.loggedIn &&
          <GoogleLogin
            clientId="517580266434-c9en3nf8itaojpvdfs3s7lldpbhoft6c.apps.googleusercontent.com"
            render={renderProps => (
              <button onClick={renderProps.onClick} disabled={renderProps.disabled}>This is my custom Google button</button>
            )}
            buttonText="Login"
            onSuccess={this.responseGoogle}
            onFailure={this.responseGoogle}
            cookiePolicy={'single_host_origin'}
            responseType='code'
          />
        }
        {this.state.loggedIn &&
          <div onClick={this.addLaunch}>
            LAUNCH!
          </div>
        }
      </div>
    );
  }
}

export default withCookies(App);